.sidebar {
	display: flex;
	flex-direction: column;
	position: relative;
	padding-top: 4rem;
	transition: all 300ms ease;
}

/* logo */
.bars {
	display: none;
}

.logo {
	display: flex;
	height: 5rem;
	align-items: center;
	justify-content: center;
	height: 2%;
}

.logo>img {
	width: 10rem;
	height: auto;
}


/* menu */
.menu {
	margin-top: 4rem;
	display: flex;
	flex-direction: column;
	gap: 2rem;
}

.menuItem {
	display: flex;
	align-items: center;
	gap: 1rem;
	height: 2.5rem;
	margin-left: 2rem;
	position: relative;
	transition: all 300ms ease;
	border-radius: 0.7rem;
	font-size: 14px;
	text-decoration: none;
	color: #000;
}

.menuItem:hover {
	cursor: pointer;
}

.active {
	background: var(--activeItem);
	margin-left: 0;
}

.active::before {
	content: "";
	width: 8px;
	height: 100%;
	background: var(--pink);
	margin-right: calc(1rem - 8px);
}


/* Tablets */
@media screen and (max-width: 1200px) {
	.menuItem>span {
		display: block;
	}
}

@media screen and (max-width: 768px) {
	.sidebar {
		position: fixed;
		z-index: 9;
		background: #ffe0e0;
		width: 55%;
		padding-right: 1rem;
		height: 100%;
	}

	.menuItem>span {
		display: block;
	}

	.logo {
		display: flex;
	}

	.bars {
		display: flex;
		position: fixed;
		top: 2rem;
		left: 60%;
		background: #ffe0e0;
		padding: 10px;
		border-radius: 10px;
		z-index: 9;
	}

	.close {
		left: -60%;
	}
}