:root {
	--yellow: linear-gradient(180deg, #F8D49A -146.42%, #FAD79D -46.42%);
	--orange: #fca61f;
	--black: #242d49;
	--gray: #788097;
	--purple: linear-gradient(180deg, #BB67FF 0%, #C484F3 100%);
	--pink: #FF919D;
	--glass: rgba(255, 255, 255, 0.54);
	--boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
	--smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
	--activeItem: #f799a354;
}

::-webkit-scrollbar {
	width: 10px;
}

::-webkit-scrollbar-track {
	background-color: rgba(169, 169, 169, 0);
}

::-webkit-scrollbar-thumb {
	background: linear-gradient(#fca71fa5, #ff919ead);
	border-radius: 20px;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	box-sizing: border-box;
}

a:active,
a:link,
a:visited {
	color: #000;
}

.App {
	/* padding: 0.5rem 3.5rem; */
	color: #242d49;
	color: var(--black);
	background: linear-gradient(106.37deg,
			#ffe1bc 29.63%,
			#ffcfd1 51.55%,
			#f3c6f1 90.85%);
	padding-top: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-family: 'Inter', sans-serif;
}

.AppGlass {
	display: grid;
	height: 97vh;
	width: 97%;
	background: rgba(255, 255, 255, 0.54);
	background: var(--glass);
	border-radius: 2rem;
	grid-gap: 16px;
	gap: 16px;
	grid-template-columns: 11rem auto;
	overflow: hidden;
}

.pageContainer {
	width: auto;
	height: auto;
	padding: 20px;
	color: #000;
	overflow-y: auto;
}

.mapGrid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px, auto));
	grid-gap: 16px;
	gap: 16px;
	grid-row-gap: 0px;
	row-gap: 0px;
}

.teamGrid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(360px, auto));
	grid-gap: 16px;
	gap: 16px;
	grid-row-gap: 0px;
	row-gap: 0px;
}

@media screen and (max-width: 1200px) {
	.AppGlass {
		grid-template-columns: 22% auto;
	}
}

@media screen and (max-width: 768px) {
	.AppGlass {
		grid-template-columns: 1fr;
	}
}
.sidebar {
	display: flex;
	flex-direction: column;
	position: relative;
	padding-top: 4rem;
	transition: all 300ms ease;
}

/* logo */
.bars {
	display: none;
}

.logo {
	display: flex;
	height: 5rem;
	align-items: center;
	justify-content: center;
	height: 2%;
}

.logo>img {
	width: 10rem;
	height: auto;
}


/* menu */
.menu {
	margin-top: 4rem;
	display: flex;
	flex-direction: column;
	grid-gap: 2rem;
	gap: 2rem;
}

.menuItem {
	display: flex;
	align-items: center;
	grid-gap: 1rem;
	gap: 1rem;
	height: 2.5rem;
	margin-left: 2rem;
	position: relative;
	transition: all 300ms ease;
	border-radius: 0.7rem;
	font-size: 14px;
	text-decoration: none;
	color: #000;
}

.menuItem:hover {
	cursor: pointer;
}

.active {
	background: var(--activeItem);
	margin-left: 0;
}

.active::before {
	content: "";
	width: 8px;
	height: 100%;
	background: var(--pink);
	margin-right: calc(1rem - 8px);
}


/* Tablets */
@media screen and (max-width: 1200px) {
	.menuItem>span {
		display: block;
	}
}

@media screen and (max-width: 768px) {
	.sidebar {
		position: fixed;
		z-index: 9;
		background: #ffe0e0;
		width: 55%;
		padding-right: 1rem;
		height: 100%;
	}

	.menuItem>span {
		display: block;
	}

	.logo {
		display: flex;
	}

	.bars {
		display: flex;
		position: fixed;
		top: 2rem;
		left: 60%;
		background: #ffe0e0;
		padding: 10px;
		border-radius: 10px;
		z-index: 9;
	}

	.close {
		left: -60%;
	}
}
