	-webkit-transform: rotate(270deg);
	        transform: rotate(270deg);
}

#time .circle svg circle {
	width: 100%;
	height: 100%;
	fill: transparent;
	stroke: #f799a354;
	stroke-width: 6;
	-webkit-transform: translate(5px, 5px);
	        transform: translate(5px, 5px);
}

