.map-box {
	position: relative;
	width: 100%;
	min-width: 300px;
	height: 80px;
	margin: 10px;
	transition: .5s;
	background: #fff;
	box-shadow: 2px 2px 10px #3f3f3f5e;
	overflow: hidden;
	border-radius: 15px;
}

.map-box .box-glas {
	position: absolute;
	top: -40px;
	left: -18%;
	width: 80%;
	height: 1000%;
	-webkit-transform: rotateZ(-16deg);
	        transform: rotateZ(-16deg);
	opacity: .7;
	z-index: 2;
	transition: all .5s;
}

.map-box .box-id {
	position: absolute;
	color: #fff;
	font-weight: 900;
	font-size: 5rem;
	z-index: 3;
	bottom: -35px;
	left: -5px;
	-webkit-transform: rotateZ(-16deg);
	        transform: rotateZ(-16deg);
}

.map-box .box-glas.dif0 {
	background-color: #e1e1e1;
}

.map-box .box-glas.dif1 {
	background-color: rgb(93, 200, 35);
}

.map-box .box-glas.dif2 {
	background-color: orange;
}

.map-box .box-glas.dif3 {
	background-color: red;
}

.map-box .box-glas.dif4 {
	background-color: rgb(255, 38, 255);
}

.map-box .box-glas.dif5 {
	background-color: #3f3f3f;
}

.map-box .box-id.dif1,
.map-box .maplvl.dif1 {
	color: rgb(93, 200, 35);
}

.map-box .box-id.dif2,
.map-box .maplvl.dif2 {
	color: orange;
}

.map-box .box-id.dif3,
.map-box .maplvl.dif3 {
	color: red;
}

.map-box .box-id.dif4,
.map-box .maplvl.dif4 {
	color: rgb(255, 38, 255);
}

.map-box .box-content {
	position: absolute;
	z-index: 3;
	padding: 5px 10px;
	font-weight: 700;
}

.mapLink:active,
.mapLink:link,
.mapLink:visited {
	color: #000;
}

.mapLink:hover .box-content {
	color: #fff;
	z-index: 101;
}

.mapLink:hover .box-glas {
	left: 10px;
	top: -200%;
}

.mapLink:hover .map-box::after {
	content: "\27A4   VIEW MAP";
	width: 100%;
	min-width: 300px;
	height: 100%;
	z-index: 100;
	position: absolute;
	top: 0;
	left: 50%;
	-webkit-transform: translate(-50%, 50%);
	        transform: translate(-50%, 50%);
	text-align: center;
	font-weight: 700;
	color: #fff;
	text-decoration: underline;
	transition: all .5s;
}

.mapLink:hover .map-box:hover::after {
	letter-spacing: 2px;
}

.mapLink:hover .map-box::before {
	content: "";
	width: 100%;
	z-index: 90;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.484);
}

.map-box .maplvl {
	position: absolute;
	z-index: 3;
	top: 5px;
	right: 10px;
	font-weight: 700;
	text-align: right;
}
.loader-container {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.loader-container span {
	margin-top: 20px;
}

.loader {
	display: inline-block;
	width: 80px;
	height: 80px;
	margin: 50px 0 20px;
}

.loader:after {
	content: " ";
	display: block;
	width: 64px;
	height: 64px;
	border-radius: 50%;
	border: 8px solid #FF919D;
	border-color: #FF919D transparent #FF919D transparent;
	-webkit-animation: loading-animation 1.2s linear infinite;
	        animation: loading-animation 1.2s linear infinite;
}

@-webkit-keyframes loading-animation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes loading-animation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
